// https://github.com/tobiasahlin/SpinKit/blob/d45b25cf9f63c8c5fe7c1115ab2f921bc924319b/scss/spinners/1-rotating-plane.scss
@keyframes sk-rotate-plane-yx {
  0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }
  50% {
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
  }
  100% {
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}

@keyframes sk-rotate-plane-xy {
  0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }
  50% {
    transform: perspective(120px) rotateX(0deg) rotateY(-180.1deg);
  }
  100% {
    transform: perspective(120px) rotateX(-179.9deg) rotateY(-180deg);
  }
}
