      }

      &:nth-child(6) {
        left: 12.75%;
        margin-top: -4%;
      }

      &:nth-child(7) {
        left: 38.25%;
        margin-top: -16.6%;
      }

      &:nth-child(8) {
        left: 63.75%;
        margin-top: -16.6%;
      }

      &:nth-child(9) {
        left: 0;
        margin-top: -4%;
      }

      &:nth-child(10) {
        left: 51%;
        margin-top: -16.6%;
      }

      &:nth-child(11) {
        left: 76.5%;
        margin-top: -16.6%;
      }

      &:nth-child(12) {
        left: 12.75%;
        margin-top: -4%;
      }

      &:nth-child(13) {
        left: 38.25%;
        margin-top: -16.6%;
      }

      &:nth-child(14) {
        left: 0;
        margin-top: -4%;
      }
    }

    .project {
      &__label {
        font-size: 1.125em;
      }
    }
  }

  @include breakpoint($screen-xxlarge) {
    .region--header {
      top: $region-padding-vert--xxlarge;
      left: $region-padding-vert--xxlarge;
    }
  }
}

.no-touchevents {
  .scene--front {
    .project {
      &__label {
        opacity: 0;
      }
    }
  }

  &.no-js {
    .scene--front {
      .project-list-item {
        &:hover {
          z-index: 100;

          .project {
            &__label {
              background: $yellow;
              opacity: 1;
            }
          }
        }
      }
    }
  }

  &.js {
    .scene--front {
      .project-list-item {
        &.hover {
          z-index: 100;

          .project {
            &__label {
              background: $yellow;
              opacity: 1;
            }
          }
        }
      }
    }
  }
}
